import { createStore } from 'vuex'                                                                                                                       
                                                                                                                       
export default createStore({                                                                                            
  state: {                                                                                            
    products: null,                                                                                            
    counter: 1,                                                                                            
    productsInfo: null,                                                                                            
    total: 0                                                                                            
  },                                                                                            
                                                                                            
  mutations: {                                                                                            
    changeCount(state, payload) {                                                                                            
      if (payload == "+") state.counter++                                                                                            
      else if (state.counter > 1) state.counter--                                                                                            
    }                                                                                            
  },                                                                                            
  actions: {                                                                                            
    getFetchproducts({ state, limit = 12 }) {                                                                                            
      fetch(`https://dummyjson.com/products?limit=${limit}`)                                                                                            
        .then(res => res.json())                                                                                            
        .then(data => {                                                                                            
          state.products = data.products                                                                                            
          state.total = data.products.length;                                                                                            
        })                                                                                            
    },                                                                                            
    getSoloProduts({ state }, id) {                                                                                            
      fetch(`https://dummyjson.com/products/${id}`)                                                                                            
        .then(res => res.json())                                                                                            
        .then(data => state.productsInfo = data)                                                                                                                       
    },                                                                                                                       
  },                                                                                                                       
  modules: {                                                                                                                       
  }                                                                                                                       
})                                                                                                                       
                                                                                                                       