<template>

    <div class="container">
      <div class="product__btns">
        <div class="product__btns1">
            <div class="product__box">
              <button class="product__btn" @click="animateBottom">By price<img class="product__btn-img" id="bottom" src="@/assets/images/arrow.svg" alt=""></button>
                <div class="product__btn2-box" id="btn">
                   <button class="product__btn2">Expensive</button>
                   <button class="product__btn2">Cheap</button>
                   <button class="product__btn2">New items</button>
                </div>
            </div>  
            
            <button class="product__btn3">Apply</button>
        </div>
        
        <button class="product__btn3">Total number of products - {{total}}</button>
    </div>
    </div>

</template>

<script>
import {  mapGetters, mapActions, mapState } from 'vuex';

export default {
  name: 'home',
  computed: {
    ...mapState(["products", "total"]),
    ...mapGetters(['filterProducts'])
  },
  methods: {
    ...mapActions(["getFetchproducts"]),
    animateBottom(){
        bottom.classList.toggle("active")
        btn.classList.toggle("active")
     }
  },
  created() {
    this.getFetchproducts()
  },
}
</script>


<style lang="scss" scoped>
.product {
 
  
  &__btns {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      padding: 80px 0;
      gap: 20px;
  }
  &__btns1 {
      max-width: 350px;
      width: 100%;
      display: flex;
     
      justify-content: space-between;
  }
  &__btn3{
    border: none;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 16px;
      padding: 12px;
      height: 43px;
      background: white;
  }
  &__btn {
    border: none;
      display: flex;
      gap: 10px;
      font-size: 16px;
      align-items: center;
      justify-content: center;
      background: white;
      padding: 12px;

      &-img {
          transition: 0.2s;
          
          &.active {
          transform: rotate(-180deg);
      }
      }
  }
  &__btn2 {
    border: none;
      font-size: 16px;
      
      &-box {    
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          gap: 12px;
          border: none;
          padding-left: 10px;
          padding-top: 10px;
          background: white;
          height: 0;
          opacity: 0;
          
          &.active {
              opacity: 1;
              height: 101px;
          }
      }
  }
  
}
</style>
