<style>

.product {
  margin: 100px 0;
  
  &__btns {
      display: flex;
      justify-content: space-between;
  }
  &__btns1 {
      max-width: 350px;
      width: 100%;
      display: flex;
      justify-content: space-between;
  }
  &__btn3{
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 16px;
      padding: 12px;
      height: 43px;
      background: white;
  }
  &__btn {
      display: flex;
      gap: 10px;
      font-size: 16px;
      align-items: center;
      justify-content: center;
      background: white;
      padding: 12px;

      &-img {
          transition: 0.2s;
          
          &.active {
          transform: rotate(-180deg);
      }
      }
  }
  &__btn2 {
      font-size: 16px;
      
      &-box {    
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          gap: 12px;
          padding-left: 10px;
          padding-top: 10px;
          background: white;
          height: 0;
          opacity: 0;
          
          &.active {
              opacity: 1;
              height: 101px;
          }
      }
  }
  &__cards {
      margin-top: 100px;
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      column-gap: 24px;
      row-gap: 100px;
  }
}
</style>